@import 'mixin';
@import 'variables';
.success {
	$ItemSize: calculateRem(54px);

	position: relative;
	display: none;
	z-index: 2;
	&.active {
		display: inline-block;
		vertical-align: middle;
		z-index: 3;
	}
	.absolute {
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		color: black;
		padding: calculateRem(4px) calculateRem(6px);
		@include font-size(10px);
	}
	.home {
		position: absolute;
		right: -8%;
		width: $ItemSize;
		height: $ItemSize;
		color: white;
		background: $QuestionRed;
		text-align: center;
		cursor: pointer;
		@include box-shadow( 0px 0px 3px rgba(0,0,0,.5));
		@include ElementVerticleAlignMiddle();
		i {
			display: inline-block;
			vertical-align: middle; 
			@include font-size(26px);
		}
		&:hover { 
			background: darken($QuestionRed, 5); 
		}
		@include border-radius(50%);
	}
	.images {
		width: 90%;
		margin: calculateRem(27px) auto 0 auto;

		img {
			display: block;
			position: relative;
			&+img {
				margin: calculateRem(24px) auto;
			}
		}
	}
	.slogan {
		background: rgba(white, .7);
		padding: calculateRem(6px) 0;
		div.content {
			padding: calculateRem(24px);
			border: 1px solid $green;
			text-align: center;
			div {
				font-family: 'w9';
				color: $Brown;
				@include font-size(30px);
				&:first-child { color: #CB3B2B; }
				&:nth-child(2) {
					line-height: 1.3;
					@include font-size(32px);
				}
				&:last-child {
					text-align: right;
					font-family: 'w3';
					padding-right: calculateRem(26px);
					@include font-size(18px);
				}
			}
		}
	}

	@media only screen and (min-width: 1440px) {
		$ItemSize: calculateRem(46px);
		.home {
			position: absolute;
			right: -8%;
			width: $ItemSize;
			height: $ItemSize;
		}
		.images {
			position: relative;
			width: 70%;
			margin: 0 auto;
			img {
				display: inline-block;
				vertical-align: middle;
				width: 40%;

				&+img {
					width: calc(60% - 1rem);
					padding-left: 3%;
				}
			}
		}
		.slogan {
			width: calc(100% + 25%);
			margin-left: -12.5%;
			div.content {
				div {
					display: inline-block;
					@include font-size(40px);
					&:nth-child(2){
						line-height: 1;
						@include font-size(40px);
						&:before {
							content: ':';
							display: inline-block;
							vertical-align: middle;
							position: relative;
							padding: 0 6px calculateRem(10px) 6px;
						}
					}
					&:last-child {
						text-align: left;
					}
				}
			}
		}
	}
}