@import 'mixin';
@import 'variables';

.questions {
	$BorderRadius: calculateRem(10px);
	background: $BackgroundOrange;
	padding: 0;
	.header {
		background: white;
		padding: calculateRem(20px) calculateRem(32px) calculateRem(14px) calculateRem(24px);
		border-top-right-radius: $BorderRadius;
		border-top-left-radius: $BorderRadius;
		line-height: 1.5;
		letter-spacing: 2px;
		color: $Brown;
		text-align: right;
		@include font-size(20px);
	}
	.questionBody {
		padding: calculateRem(20px) calculateRem(20px) 0;
		border-bottom: 1px solid transparent;
		border-image: url(../images/border_horizontal.png) 2 round;
		.title {
			font-family: 'w5';
			font-weight: 600;
			color: white;
			text-align: center;
			@include font-size(28px);
			letter-spacing: 3px;
			margin-bottom: calculateRem(14px);
			i { margin-left: -4px; }
		}
		.owl-carousel {
			.item {
				background: $DeepGray;
				$size: 21rem;
				width: 100%;
				height: $size;
				margin: 0 auto;
				// height: 20.8183rem;
				overflow: hidden;
				@include border-radius(7px);
				.img {
					border-top-right-radius: 7px;
					border-top-left-radius: 7px;
					overflow: hidden;
				}
				.CommonBlock {
					.inlineBlock {
						vertical-align: middle;
						&.icon  div {
							background: $SpeakerGreen;
							color: white;
						}
						&.click div {
							background: darken($SpeakerGreen, 10);
						}
					}
				}
			}
		}
		.owl-buttons {
			text-align: center;
			margin: calculateRem(20px) auto;
			$buttonSize: calculateRem(32px);
			button {
				color: white;
				cursor: pointer;
				display: inline-block;
				vertical-align: middle;
				background: rgba(white, .2);
				&:hover, &.click {
					background: rgba(white, .4);
					@include box-shadow(0 0 4px rgba(black, .1));
				}
				@include transition(all .1s linear);
				@include box-shadow(0 0 2px rgba(black, .05));
			}
			button:not(.submit) {
				width: $buttonSize;
				height: $buttonSize;
				text-align: center;
				@include font-size(16px);
				@include border-radius(50%);

				i { display: inline-block; }
				&:first-child i { margin-left: calculateRem(-5px); }
				&:last-child i { margin-right: calculateRem(-5px); }
			}
			.submit {
				height: $buttonSize;
				@include font-size(16px);
				margin: 0 calculateRem(10px);
				letter-spacing: 1.5px;
				padding: calculateRem(4px) calculateRem(24px);
			}
		}	
	}
	@media only screen and (min-width: 375px) {
		.questionBody .owl-carousel .item {
			width: 21rem;

			.CommonBlock {
				position: relative;
				z-index: 2;
				background: $DeepGray;
				margin-top: -1.6rem;
			}
		}
	}
	@media only screen and (min-width: 425px) {
		.questionBody .owl-carousel .item  .CommonBlock { margin-top:0; }
	}
	@media only screen and (min-width: 1440px) {
		$BorderRadius: calculateRem(15px);
		.header {
			width: 100%;
			text-align: right;
			border-top-right-radius: $BorderRadius;
			border-top-left-radius: $BorderRadius;
			@include font-size(20px);
			div {
				display: inline-block;
				&:first-child { margin-left: 0; }
				&:last-child { margin-left: 0; }
			}
		}

		.questionBody {
			position: relative;
			padding: calculateRem(60px) 0 1.4rem 0;
			width: 80%;
			margin: 0 auto;
			border-bottom: 3px solid transparent;
			border-image: url(../images/border_horizontal.png) 3 round;
			.title { display: none; }
			.owl-carousel {
				.item {
					background: transparent;
					width: auto;
					height: auto;
					@include border-radius(10px);
					.img, .CommonBlock {
						display: inline-block;
						vertical-align: top;
						width: calc(50% - .5rem);
						img { @include border-radius(10px); }
					}
					.CommonBlock {
						background: transparent;
						position: inherit;
						z-index: 1;
						.pc {
							font-family: 'w5';
							display: block;
							color: white;
							margin-bottom: calculateRem(20px);
							@include font-size(28px);
						}
						.inlineBlock {
							display: block;
							text-align: left;
							&.icon {
								width: auto;
								position: absolute;
								bottom: 0;
								right: 0;
								text-align: center;
							}
							&.wording {
								width: 100%;
								letter-spacing: 1.8px;
								@include font-size(16px);
							}
						}
					}
				}
			}
			.owl-buttons {
				margin: calculateRem(26px) auto;
				$buttonSize: calculateRem(40px);
				button:not(.submit) {
					width: $buttonSize;
					height: $buttonSize;
					@include font-size(26px);
				}
				.submit {
					height: $buttonSize;
					@include font-size(18px);
				}
			}
		}
	}
}