@import 'mixin';
@import 'variables';

.anwsers {
	padding: 20% calculateRem(20px);
	background: $BackgroundOrange;
	.wrapper {
		display: table;
		width: 100%;
		margin: 0;
		padding: 0;
		background: transparent;
		border-collapse: collapse;
		overflow: hidden;
		@include border-radius(5px);
		.CommonBlock {
			height: 7.5rem;
			.inlineBlock {
				padding: calculateRem(12px); 
				&.icon div { background: white; }
				&.wording { color: white; }
			}
		}
	}
	.question {
		.CommonBlock {
			display: table-row;
			.inlineBlock {
				display: table-cell;
				vertical-align: middle;
				&.icon {
					background: rgba(47,44,43, 1);
					div { color: rgba(47,44,43, 1); }
				}

				&.click div {
					background: $Gray;
					color: white;
				}

				&.wording {
					background: white;
					color: rgba(47,44,43, 1);
				}
			}
		}
	}
	.title {
		width: 70%;
		margin: calculateRem(32px) auto calculateRem(8px) auto;
		text-align: center;
		color: $DeepGray;
		@include font-size(18px);
	}
	.options {
		padding: calculateRem(18px) 0 calculateRem(12px) 0;
		border-bottom: 2px solid transparent;
		border-top: 2px solid transparent;
		border-image: url(../images/border_horizontal.png) 2 round;
		.tableRow {
			$classes    : true, false;
			$background : $QuestionRed, $QuestionOrange;

			@for $i from 1 through 2 {
				.#{nth($classes, $i)} {
					display: block;
					cursor: pointer;
					background: nth($background, $i);
					@include border-radius(7px);
					@if nth($classes, $i) == false { margin-top: calculateRem(10px); }
					.wrapper {
						display: block;
					}
					.CommonBlock {
						display: inline-block;
						vertical-align: middle;
						width: 100%;
						padding: 0;
						@include ElementVerticleAlignMiddle();
						&:after {
							content: '';
							display: block;
							position: absolute;
							top: 0;
							left: 19%;
							width: 1px;
							border-right: 2px solid transparent;
							border-image: url(../images/border_vertical.png) 2 round;
							height: 100%;
						}
						.inlineBlock {
							&.icon {
								padding: 0;
								div {
									color: nth($background, $i);
									&:hover { color: darken(nth($background, $i), 5); }
								}
								&.click div {
									color: white;
									background: $Gray;
								}
							}
						}
					}
					&.click { background: darken(nth($background, $i), 5); }
				}
			}
		}
	}

	@media only screen and (min-width: 1440px) {
		padding: 10% 0;
		.wrapper {
			width: 40%;
			margin: 0 auto;
			@include border-radius(10px);
			.CommonBlock .inlineBlock {
				&.wording {
					@include font-size(16px);
				}
			}
		}
		.options {
			border-bottom: 3px solid transparent;
			border-top: 3px solid transparent;
			border-image: url(../images/border_horizontal.png) 3 round;
			width: 70%;
			margin-left: auto;
			margin-right: auto;
			display: table;
			height: auto;
			padding: 0;
			background: transparent;
			border-collapse:separate;
			border-spacing: calculateRem(20px);
			overflow: hidden;
			.tableRow {
				display: table-row;
				.true, .false {
					display: table-cell;
					vertical-align: middle;
					width: 50%;
					position: relative;
					&:before {
						content: '';
						display: block;
						position: absolute;
						top: 2.5%;
						left: 19%;
						width: 1px;
						border-right: 3px solid transparent;
						border-image: url(../images/border_vertical.png) 3 round;
						height: 95%;
					}
					.CommonBlock:after { content: none; }
					.wrapper {
						width: 100%;
						height: 100%;
					}
				}
			}
		}
	}
}