@import 'mixin';
@import 'variables';

.phone {
	$ItemSize: calculateRem(66px);
	$BorderRadius: 10px;
	background: white;
	padding: 20% 2rem 8% 2rem;
	.line {
		border: 1px solid #CCC;
		color: darken($DeepGray, 5);
		padding: calculateRem(18px) calculateRem(24px);
		width: 100%;
		text-align: center;
		margin-bottom: calculateRem(20px);
		overflow-y: scroll;
		letter-spacing: 1px;
		@include font-size(26px);
		@include border-radius(7px);
	}
	.buttons {
		position: relative;
		text-align: center;
		width: 95%;
		margin: 0 auto;
		z-index: 1;
		ul.numbers li {
			font-family: 'w7';
			display: inline-block;
			vertical-align: middle;
			width: $ItemSize;
			height: $ItemSize;
			padding-top: calculateRem(6px);
			margin-right: calc((100% - (#{$ItemSize} + .5rem) * 3) / 2);
			margin-bottom: calculateRem(12px);
			background: $LightGray;
			color: black;
			cursor: pointer;
			@include border-radius(50%);
			&:nth-child(3n+3) {
				margin-right: 0;
			}
			&:hover, &.active {
				background: lighten($HoverGray, 20);
			}
			div:not(.small) {
				@include font-size(46px);
			}
			div.small {
				font-family: 'w3';
				margin-left: 2px;
				font-weight: 300;
				letter-spacing: 1px;
				color: darken($HoverGray, 1);
				@include font-size(10px);
			}
			&:nth-child(10), &:nth-child(12) {
				padding-top: 0;
				@include ElementVerticleAlignMiddle();
				text-align: center;
				div:not(.small) {
					display: inline-block;
					vertical-align: middle;
				}
			}
			&:nth-child(10) div:not(.small) { 
				padding-top: .6rem;
				font-size: 3.9rem 
			}
		}
		.submit {
			cursor: pointer;
			width: $ItemSize;
			height: $ItemSize;
			margin: calculateRem(12px) auto;
			background: $PhoneGreen;
			color: white;
			font-size: 2.5rem;
			padding: 0;
			@include border-radius(50%);
			&:hover, &.click {
				background: $HoverGreen;
			}
		}
		.loading {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 2;
		}
	}

	@media only screen and (min-width: 340px) {
		$ItemSize: calculateRem(72px);
		background: white;
		padding: 15% 2.5rem 8% 2.5rem;
		.buttons {
			ul.numbers li {;
				width: $ItemSize;
				height: $ItemSize;
				margin-right: calc((100% - (#{$ItemSize} + .5rem) * 3) / 2);
			}
			.submit {
				width: $ItemSize;
				height: $ItemSize;
			}
		}
	}
	
	@media only screen and (min-width: 400px) {
		$ItemSize: calculateRem(68px);
		background: white;
		padding: 15% 3.5rem 8% 3.5rem;
		.buttons {
			ul.numbers li {;
				width: $ItemSize;
				height: $ItemSize;
				margin-right: calc((100% - (#{$ItemSize} + .5rem) * 3) / 2);
			}
			.submit {
				width: $ItemSize;
				height: $ItemSize;
			}
		}
	}

	@media only screen and (min-width: 1440px) {
		$ItemSize: calculateRem(65px);
		background: white;
		.line {
			@include font-size(22px);
			@include border-radius(10px);
		}
		.buttons {
			ul.numbers li {;
				width: $ItemSize;
				height: $ItemSize;
				margin-right: calc((100% - (#{$ItemSize} + .5rem) * 3) / 2);
				div:not(.small) {
					font-size: calculateRem(40px);
				}
				div.small {
					font-size: calculateRem(12px);
				}
				&:nth-child(10) div:not(.small) {
					font-size: calculateRem(46px);
				}
			}
			.submit {
				width: $ItemSize;
				height: $ItemSize;
				margin: calculateRem(12px) auto;
				font-size: calculateRem(40px);
			}
		}
	}
}