@import 'reset';
@import 'mixin';
@import 'variables';

// 從 mobile 至 pc
$pc_width     : 1920;
$pad_width    : 768;
$pad_landscape: 1080;
$mobile_width : 300;

@for $i from 300 through $pc_width{
	@if ($i % 60 == 0) {
		@if $i <= 1399 {
			@media only screen and (min-width: #{$i}px) {
			    html{
			        font-size: ((33px / 1080) * $i);
			    }
			}
		} @else if $i <= 2080 {
			@media only screen and (min-width: #{$i}px) {
			    html{
			        font-size: ((24px / 1920) * $i);
			    }
			}
		}
	}
}


@font-face {
	font-family: "w3";
	src: url('../fonts/DFYuanStd-W3.otf');
};
@font-face {
	font-family: "w5";
	src: url('../fonts/DFYuanStd-W5.otf');
};
@font-face {
	font-family: "w7";
	src: url('../fonts/DFYuanStd-W7.otf');
};
@font-face {
	font-family: "w9";
	src: url('../fonts/DFYuanStd-W9.otf');
};

body {
	font-family: 'w3', 'w5', 'w7', 'w9', 'Roboto', 'PingFang TC', Arial, sans-serif;
	line-height: 1;
	font-weight: normal;
	margin: 0;
	padding: 0;
	background-image: url("../images/background_phone.png");
	background-color: white;
	background-repeat-y: repeat;
	background-size: 100%;
	position: relative;
	min-height: 100vh;
	height: 100vh;
	text-align: center;
	@include ElementVerticleAlignMiddle();

	&.vertical { background-image: url("../images/background_phone.png"); };
	&.horizontal { background-image: url("../images/background_pc.png"); };

	img {
		position: relative;
		width: 100%;
	}
	button, input {
		outline: none !important;
		background: transparent;
		border: none;
	}

	.inlineBlock { display: inline-block; }
	.CommonBlock {
		position: relative;
		border-bottom-right-radius: 7px;
		border-bottom-left-radius: 7px;
		padding: calculateRem(12px);
		.inlineBlock {
			vertical-align: middle;
			&.icon {
				text-align: center;
				width: 20%;

				div {
					@include ElementVerticleAlignMiddle();
					$size: calculateRem(40px);
					width: $size;
					height: $size;
					margin: 0 auto;
					cursor: pointer;
					@include border-radius(50%);
					@include transition(all .1s linear);
					i {
						display: inline-block;
						vertical-align: middle;
						@include font-size(26px);
					}
				}
			}
			&.wording {
				width: calc(80% - .5rem);
				color: white;
				letter-spacing: .5px;
				line-height: 1.5;
				text-align: left;
				@include font-size(14px);
			}
		}
	}

	.app {
		display: inline-block;
		vertical-align: middle;
		text-align: left;
		position: relative;
		width: 75%;
		margin: 20px auto;
		@include ElementVerticleAlignMiddle();
		$BorderRadius: calculateRem(10px);
		.container {
			position: relative;
			display: none;
			vertical-align: middle;
			z-index: 2;
			&.active {
				display: inline-block;
				z-index: 3;
			}
			.header {
				position: relative;
				z-index: 2;
				.absolute {
					position: absolute;
					z-index: 1;
					color: black;
					padding: calculateRem(2px);
					top: 0;
					left: 0;
					@include font-size(10px);
				}
				img {
					width: 95%;
					margin: 0 auto;
				}
			}
			.body {
				position: relative;
				width: 95%;
				height: 37rem;
				margin: -17% 0 0 4%;
				z-index: 1;
				@include border-radius($BorderRadius);
				@include box-shadow();
				.phone, .questions, .anwsers {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					padding: 15%;
					z-index: 1;
					opacity: 0;
					@include transition(all .1s ease-out);
					@include border-radius($BorderRadius);

					&.active {
						display: block;
						opacity: 1;
						z-index: 3;
					}
				}
				
				@import 'phone';
				@import 'questions';
				@import 'anwsers';
				
			}
		}
		@import 'popup';
		@import 'success';
		.health_bureau {
			width: 75%;
			margin: calculateRem(20px) auto 0 auto;
		}
		.pc { display: none; }
	}

	@media only screen and (min-width: 1440px) {
		.app {
			$BorderRadius: calculateRem(15px);
			.health_bureau {
				width: 50%;
				margin: calculateRem(20px) auto 0 auto;
			}
			.container{
				.header .absolute {
					border: 1px solid black;
				}
				.body {
					@include border-radius($BorderRadius);
					.phone, .questions, .anwsers {
						@include border-radius($BorderRadius);
					}
				}
			}

			&:not(.index) {
				width: 80%;
				.container {
					.header {
						width: 60%;
						margin: 0 auto;
					}
					.body {
						margin: -8% auto 0 auto;
						height: 32rem;
					}
				}
				.health_bureau {
					width: 20%;
				}

				.popup {
					.content {
						width: 50%;
					}
				}

				.success {
					.images {
						position: relative;
					}
					.absolute {
						top: 30%;
						left: -8%;
					}
				}
			}

			&.index {
				width: 30%;
				.container .body { height: 34rem; }
			}
		}
		
	}
}

